import React from 'react'
import { Searchbar, Page, Navbar, NavTitle, NavRight, NavLeft, Link, Subnavbar, Toolbar } from 'framework7-react'
import { HeadProvider, Title, Meta } from 'react-head'


export function AdminLayout({children, title = 'My Denote', description = ''})
{
    const color = '#009688'

    return (
        <>
            <HeadProvider>
                <Title>{title}</Title>
                {/*<Links rel="manifest" href="/manifest.json" />*/}
                <Meta property="og:type" content="website" />
                <Meta property="og:site_name" content="Mobius Trade" />
                <Meta property="og:image" content="/img/logo.png" />
                <Meta property="og:title" content={title} />
                {/*<Meta property="og:url" content={`https://mobius.com${location.pathname}`} />*/}
                
                <Meta property="og:description" content={description} />
                <Meta name="author" content="Mobius ltd." />
                <Meta name="description" content={description} />
                <Meta name="theme-color" content={color} />
                <Meta name="msapplication-navbutton-color" content={color} />
                <Meta name="apple-mobile-web-app-status-bar-style" content={color} />
            </HeadProvider>
            <Page colorTheme='teal' color="white" hideToolbarOnScroll> 
                <Navbar className="bg-teal" color="white">
                    <NavLeft>
                        <Link panelOpen="left">
                            <span className="material-icons">
                                menu_open
                            </span>
                        </Link>
                    </NavLeft>
                    <NavTitle className="m-0">
                        <strong>{title}</strong>
                    </NavTitle>
                    <NavRight>
                        <Link href="/panel-right/">
                            <i className="f7-icons" data-icon="gear_alt_fill"></i>
                        </Link>
                    </NavRight>

                    <Subnavbar inner={false}>
                        <Searchbar
                            placeholder="Поиск..."
                            searchContainer=".search-list"
                            searchIn=".item-title"
                            //disableButton={!theme.aurora}
                        />
                    </Subnavbar>
                </Navbar>
                {children}
                <Toolbar tabbar  bottom color="white">
                    <Link href="/edituser/">
                        <i className="material-icons">border_color</i>
                    </Link>
                    <Link href="/form/">
                        <i className="material-icons">add_circle_outline</i>
                    </Link>
                    <Link>
                        <i className="material-icons">group_add</i>
                    </Link>
                    <Link>
                        <i className="material-icons">get_app</i>
                    </Link>
                </Toolbar>
            </Page>
        </>
    )
}