import React from 'react'
import { List, ListItem, SwipeoutActions, SwipeoutButton } from 'framework7-react'
import { AdminLayout } from '../AdminLayout'
import DataList from '../data/List'

export default function EditPage()
{
    const users = DataList()
    const more = (e) => {
        console.log('Action: ', e)
    }
    return (
        <AdminLayout title="Редактировать">
            <List className="searchbar-not-found">
                <ListItem title="Nothing found"></ListItem>
            </List>
            {users.length && (
            <List mediaList className="mt-0 search-list searchbar-found">
            {users.map((user, inx) => (
                <ListItem 
                    key={inx} 
                    swipeout
                    title={user.title} 
                    subtitle={user.subtitle} 
                    badgeColor={user.badgeColor} 
                    badge={user.group}
                    footer={user.footer}
                >
                <SwipeoutActions left>
                    <SwipeoutButton overswipe color="blue" href={`/contact/user/${user.link}`}>
                        <span className="material-icons">create</span>
                    </SwipeoutButton>
                </SwipeoutActions>
                <SwipeoutActions right>
                    <SwipeoutButton onClick={more}>
                        <span className="material-icons">remove_red_eye</span>
                    </SwipeoutButton>
                    <SwipeoutButton
                        delete
                        overswipe
                        confirmText="Are you sure you want to delete this item?"
                    >
                        <span className="material-icons">delete</span>
                    </SwipeoutButton>
                </SwipeoutActions>
                


                    <img
                        className="contact__img"
                        slot="media"
                        alt={user.title}
                        src={user.src}
                        width="44"
                    />
                </ListItem>
            ))}
            </List>
        )}
        </AdminLayout>
    )
}