import React, { useState } from 'react'
import { Page, Navbar, List, ListItem, BlockTitle, Block } from 'framework7-react'


export default function LanguagePage() 
{
    const Lang = [
        {img: 'ru', name: 'Русский', checked: true},
        {img: 'en', name: 'English', checked: false},
        {img: 'ch', name: '中文', checked: false},
        {img: 'es', name: 'Eestlane', checked: false},
        {img: 'it', name: 'Italiano', checked: false}
    ]

    const [currlang, setCurrlang] = useState('Русский') 
    const [select, setSelect] = useState('Русский') 
    const currency = (e) => {
        setSelect(e.target.value)
    }

    return (
    <Page colorTheme="teal">
        <Navbar title="Локализация" backLink="Back" color="white"></Navbar>
        <BlockTitle>
            <strong><h2>Выберите язык:</h2></strong>
        </BlockTitle>
        <List mediaList>
            {Lang.map((lan, inx) => (
            <ListItem key={inx}
                radio 
                radioIcon="end" 
                title={lan.name} 
                value={lan.img} 
                name="lang" 
                defaultChecked={lan.checked} 
                onClick={() => setCurrlang(`${lan.name}`)}
                onChange={currency}
            >
                <img 
                    src={`/flags/${lan.img}.svg`} 
                    alt={lan.img} 
                    width="30"
                    slot="media"
                />
            </ListItem>            
            ))}
        </List>
        <Block><b>Текущий язык:</b> {currlang}</Block>
        <Block>{select}</Block>
    </Page>
    )
}