import React, { Component } from 'react'
import { Page } from 'framework7-react'
import { CartUser } from '../helper/CartUser'


export default class ContactList extends Component 
{
    constructor(props) {
        super(props);
        this.href = Object.keys(this.$f7route.params).map(key => this.$f7route.params[key])
    }
    render() {
        return (
            <Page colorTheme='teal'>
                <CartUser user={this.href} />
            </Page>
        );
  }
}