import React, { useState } from 'react';
import { 
  //f7, 
  Page, LoginScreenTitle, List, ListInput, Button, BlockFooter, Block, Row, Col, Card, CardContent } from 'framework7-react'
import { ComponyData } from '../../data/ComponyData'


// { f7router }
export default () => {
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [isLoading, setIsLoading] = useState(false)
  //const [login, setLogin] = useState(true)
  const data = ComponyData()
  const signIn = () => {
    //if(username === '') {
    //  f7.dialog.alert(`Вы не ввели логин`, () => {
    //    f7router.back();
    //  })
    //} else if(username !== 'test@test.ru') {
    //  f7.dialog.alert(`Не верно указан логин`, () => {
    //    f7router.back();
    //  })
    //} else if(password !== 'qwerty') {
    //  f7.dialog.alert(`Не верно указан пароль`, () => {
    //    f7router.back();
    //  })
    //} else if(password === '') {
    //  f7.dialog.alert(`Вы не указали пароль`, () => {
    //    f7router.back();
    //  })
   // } else {
    //  setLogin(true)
    //}


    //f7.dialog.alert(`Username: ${username}<br>Password: ${password}`, () => {
    //  f7router.back();
    //})
  }

  const load = () => {
    if (isLoading) return;
    setIsLoading(true);
    setTimeout(() => {
      setIsLoading(false)
      signIn()
    }, 4000);
  }

  return (
    <Page noToolbar noNavbar noSwipeback loginScreen colorTheme="teal">
        <LoginScreenTitle>
            <h2>{data[0].name}</h2>
        </LoginScreenTitle>
      <Card style={{background: 'transparent'}}>
      <CardContent>
        <List form>
            <ListInput
              label="Username"
              type="text"
              placeholder="Your username"
              value={username}
              onInput={(e) => {
                setUsername(e.target.value);
              }}
            />
            <ListInput
              label="Password"
              type="password"
              placeholder="Your password"
              value={password}
              onInput={(e) => {
                setPassword(e.target.value);
              }}
            />
        </List>
        <List>
          <Block strong>
            <Row>
              <Col className="mt-1">
                <Button fill round onClick={load} preloader loading={isLoading}>
                  <i className="f7-icons size-14" data-icon="square_arrow_right"></i>
                  &#160;Sign In
                </Button>
              </Col>
            </Row>
          </Block>
        </List>
      </CardContent>
    </Card>
    <BlockFooter className="mt-5">
      <p>&copy; {data[0].name}, {new Date().getFullYear()}</p>
      <small>Все права защищены</small>
    </BlockFooter>
    </Page>
  );
}