import React from 'react'
import { List, ListItem, Navbar, NavRight, Link } from 'framework7-react'
import DataList from '../data/List'

export function CartUser(props)
{
    const User = DataList()
    const result = User.filter(p => p.link.includes(`${props.user}`))
    const Name = result.map(user => user.title)

    return (
        <>
            <Navbar title={Name} backLink="Back" color="white">
                <NavRight>
                    <Link href="/panel-right/">
                        <i className="f7-icons" data-icon="gear_alt_fill"></i>
                    </Link>
                </NavRight>
            </Navbar>
            {result.map((user, inx) => (
                <List mediaList key={inx}>
                    <ListItem 
                        title={user.title} 
                        footer={user.footer}
                        link="#"
                        after="ред."
                    >
                        <img
                            className="contact__img"
                            slot="media"
                            alt={user.title}
                            src={user.src}
                            width="44"
                        />
                    </ListItem>
                    <ListItem divider title="Контакты"></ListItem>
                    <ListItem after="телефон">
                        <a slot="media" href={`tel:${user.subtitle}`} className="link external" target="_blank" rel="noopener noreferrer">
                            <i className="f7-icons color-muted" data-icon="phone"></i> {user.subtitle}
                        </a>
                    </ListItem>
                    {
                        user.email !== '' ?
                        <ListItem after="E-mail">
                            <a slot="media" href={`mailto:${user.email}`} className="link external" target="_blank" rel="noopener noreferrer">
                                <i className="f7-icons if7-email color-muted" data-icon="at"></i> {user.email}
                            </a>
                        </ListItem> : ''                      
                    }
                </List>
                ))}
                {/*
                    <p>{props.user}</p>
                    <pre>{JSON.stringify(result, null, 2)}</pre>
                */}
        </>
    )
}