import React, { useState, useEffect } from 'react'
import { Page, Navbar, List, ListItem, BlockTitle, Block } from 'framework7-react'


export default function ThemePage() 
{
    const [theme, setTheme] = useState('dark')

    useEffect(() => {
        window.localStorage.setItem('theme', theme)
    }, [theme])

    return (
        <Page colorTheme="teal">
            <Navbar title="Оформление" backLink="Back" color="white"></Navbar>
            <BlockTitle>
                <strong><h2>Выберите тему:</h2></strong>
            </BlockTitle>
            <List>
                <ListItem
                    radio 
                    radioIcon="end"
                    title="Тёмная" 
                    value="dark"
                    name="theme"
                    defaultChecked
                    onClick={() => setTheme('dark')}
                ></ListItem>
                <ListItem
                    radio 
                    radioIcon="end"
                    title="Светлая" 
                    value="light"
                    name="theme"
                    onClick={() => setTheme('light')}
                ></ListItem>
            </List>
            <Block>
                <p>Текущая тема: {theme}</p>
            </Block>
        </Page>
    )
}