import React from 'react'
import { HeadProvider, Title } from 'react-head'
import { Page, Navbar, Block, NavLeft, NavTitle, Link } from 'framework7-react'
     //  backLink="Back"

export default function NotFoundPage() 
{
    const title = 'Page Not Found'
    return (
        <>
            <HeadProvider>
                <Title>{title}</Title>
            </HeadProvider>
            <Page>
                <Navbar className="bg-teal" color="white">
                    <NavLeft>
                        <Link href="/">
                            <i class="f7-icons" data-icon="chevron_left"></i>
                        </Link>
                    </NavLeft>
                    <NavTitle>
                        <strong>{title}</strong>
                    </NavTitle>
                </Navbar>
                <Block strong>
                    <p>Sorry</p>
                    <p>Requested content not found.</p>
                </Block>
            </Page>
        </>
    )
}